

























  import Vue from 'vue';
  import axios from 'axios'

  export default Vue.extend({

    name: 'PlatformList',

    methods: {

      overview(abb: any) {
        this.$router.push({
          name: 'gamesbyplatform',
          params: {
            abb: abb
          }
        })
      }

    },

    data() {
      return {
        platforms: [],
        url: 'https://classicgamedb.herokuapp.com/platforms'
      }
    },

    mounted() {

      axios.get(this.url)
        .then(response => {
          this.platforms = response.data
        })
        .catch(function (error) {
          if (error.response) {
            console.log(error.response);
          }
        })

    },

  });
